<template>
  <div id="app">
    <head>
          <link href="https://fonts.googleapis.com/icon?family=Material+Icons" rel="stylesheet">
    </head>
    <header id="main-header">
      <img id="logo" srcset="./assets/logo-dummy.png">
      <ul id="header-menu">
        <li class="menu-item"><a class="icon-text" ref=""><i class="material-icons md-24">home</i>ホーム</a></li>
        <li class="menu-item"><a class="icon-text" ref=""><i class="material-icons md-24">note_add</i>投稿</a></li>
        <li class="menu-item"><a class="icon-text" ref=""><i class="material-icons md-24">account_box</i>マイページ</a></li>
      </ul>
    </header>
    <div class="search-box icon-text">
      <i class="material-icons md-36">search</i><textarea class="search-area" placeholder="日本酒の名前や産地を入力してください"></textarea>
    </div>
    <!-- <img src="./assets/logo.png"> -->
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style>
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
  margin: 0px 30px;
}

.icon-text {
  display: flex;
  justify-content: center;
  align-items: center;
}

#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

#main-header {
  z-index: 1;
  position: fixed;
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 0px 50px;
  background-image: url("./assets/header-img.jpg");
}

#logo {
  height: 80px;
}

#header-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.search-box {
  z-index: 1;
  height: 40px;
  width: 100%;
  margin: 80px 0px;
  position: fixed;
  background-color: white;
  display: flex;
  align-items: center;
}

.search-area {
  outline: 0;
  width: 80vh;
  height: 40px;
  line-height: 39px;
  resize: none;
  border: 0px;
  font-size: 22px;
}

.material-icons {
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 24px; /* 推奨サイズ */
  display: inline-block;
  width: 1em;
  height: 1em;
  line-height: 1;
  text-transform: none;
}

/* アイコンサイズ */
.material-icons.md-18 {
  font-size: 18px;
}
.material-icons.md-24 {
  font-size: 24px;
}
.material-icons.md-36 {
  font-size: 36px;
}
.material-icons.md-48 {
  font-size: 48px;
}

/* 背景が明るいとき用のアイコン色 */
.material-icons.md-dark {
  color: rgba(0, 0, 0, 0.54);
}
.material-icons.md-dark.md-inactive {
  color: rgba(0, 0, 0, 0.26);
}

/* 背景が暗いとき用のアイコン色 */
.material-icons.md-light {
  color: rgba(255, 255, 255, 1);
}
.material-icons.md-light.md-inactive {
  color: rgba(255, 255, 255, 0.3);
}
</style>



